import { Component,OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  heroForm: FormGroup;

  ngOnInit(): void{
    this.heroForm = new FormGroup({
      'name': new FormControl('',[
        Validators.required,
      Validators.minLength(3),
    Validators.maxLength(32)
  ]),
    'email' : new FormControl('',[Validators.required, Validators.email]),
    'company': new FormControl('',[Validators.required,Validators.pattern('^[a-zA-Z0-9]+$')]),
    'subject' : new FormControl('',[Validators.required,Validators.minLength(1),Validators.maxLength(15)]),
    'message' : new FormControl('',[Validators.required,Validators.minLength(30),Validators.maxLength(150)])
    })

    }
    onSubmit(){
      alert("Contact form has been submitted successfully");
    }
  }
